/*FIRST HEADER BEM BLOCKS START */

.email {
  position: relative;
  margin-right: auto;
  color: inherit;
  text-decoration: inherit;
}

.email::before {
  position: absolute;
  top: -2px;
  left: -28px;
  width: 21px;
  height: 20px;
  content: "";
  display: block;
  background-position: -239px -192px;
  background-image: url(../images/spritesheet.png);
  background-repeat: no-repeat;
}

.email:hover {
  color: #e5493a;
}

.email:hover::before {
  background-position: -140px -222px;
}

.changeLanguage {
  position: relative;
  margin-right: 75px;
  margin-left: 20px;
  color: inherit;
  text-decoration: inherit;
  border: none;
  background: inherit;
  font-family: inherit;
}

.changeLanguage::before {
  position: absolute;
  top: 0;
  left: -20px;
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../images/spritesheet.png);
  background-repeat: no-repeat;
  background-position: -71px -87px;
}

.changeLanguage::after {
  position: absolute;
  content: "";
  display: block;
  right: -7px;
  top: 5px;
  background-color: transparent;
  border: solid  #363636;
  border-width: 0 2px 2px 0;
  width: 5px;
  height: 5px;
  transform: rotate(45deg);
}

.changeLanguage:hover:before {
  background-position: -99px -87px;
}

.changeLanguage:hover::after {
  position: absolute;
  content: "";
  display: block;
  right: -7px;
  top: 5px;
  background-color: transparent;
  border: solid #e5493a;
  border-width: 0 2px 2px 0;
  width: 5px;
  height: 5px;
  transform: rotate(45deg);
}

