.navigation {
  margin-left: auto;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  font-weight: 600;
  color: #707070;
  text-transform: uppercase;
}

.navigation__menu {
  list-style-type: none;
  display: inline-block;
}

.navigation__item {
  list-style-type: none;
  display: inline-block;
  position: relative;
  margin-left: 43px;
  transition: 0.6s all;
}

.navigation__item::before {
  position: absolute;
  top: -3px;
  border-top: 2px solid #eb342c;
  width: 0px;
  height: 1px;
  content: "";
}

@keyframes line {
  10% {
    width: 2px;
  }

  100% {
    width: 70px;
  }

  1000% {
    width: 70px;
  }
}

.navigation__item:hover {
  color: #eb342c;
}

.navigation__item:hover::before {
  animation: line 0.8s ease-out;
  width: 70px;
}

.navigation__item--list {
  transition: 0.5s all;
  position: relative;
}

.navigation__item--list:hover::after {
  background-color: transparent;
  border: solid  #eb342c;
  border-width: 0 2px 2px 0;
  transition: 0.5s all;
}

.navigation__item--list::after {
  transition: 0.5s all;
  position: absolute;
  content: "";
  display: block;
  right: -14px;
  top: 3px;
  background-color: transparent;
  border: solid  #6c6c6c;
  border-width: 0 2px 2px 0;
  width: 5px;
  height: 5px;
  transform: rotate(45deg);
}

.navigation__item a {
  color: inherit;
  text-decoration: none;
}

.navigation__subMenu {
  list-style-type: none;
  padding: 0;
  right: -105px;
  top: 15px;
  position: absolute;
  display: none;
  background-color: white;
  padding: 10px;
  width: 80px;
  transition: 0.8s all;
}

.navigation__subItem {
  padding: 0;
  color: #eb342c;
  margin-top: 5px;
}

.navigation__subItem a {
  color: inherit;
  text-decoration: none;
}

